import styled from "styled-components";
import { motion, AnimatePresence, useScroll } from "framer-motion"
import { useState } from "react";
import { useQuery } from "@tanstack/react-query";
import getData from "lib/api/api";
import makeImagePath from "lib/utils/makeImagePath";
import { useMatch, useNavigate } from "react-router-dom";
import { ForwardOutlined } from "@ant-design/icons";

const Slider = styled.div`
    position: relative;
    top: -220px;
`

const Row = styled(motion.div)`
    display: grid;
    gap : 10px;
    grid-template-columns: repeat(6,1fr);
    position: absolute;
    width: 100%;
`

const Box = styled(motion.div)`
    background-image: url(${(props)=>props.bgphoto});
    height: 200px;
    background-size: cover;
    background-position: center center;
    border-radius: 15%;
    box-shadow: 3px -3px 4px black;
    cursor: pointer;
    &:first-child{
        transform-origin: center left;
    }
    &:last-child{
        transform-origin: center right;
    }
`;

const Button = styled.div`
    width: 50px;
    height: 50px;
    position: absolute;
    z-index: 99;
    top:68%;
    left:50%;
    transform: translate(-50%,-50%);
    font-size: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #3e3232;
    font-size: 40px;
    padding-left: 4px;
    background-color: rgba(255,255,255,0.5);
    border-radius: 50%;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.1s;
    &:hover{
        font-size: 55px;
    }
`

const Info = styled(motion.div)`
    padding : 10px;
    background-color: #2F2F2F;
    opacity: 0;
    position: absolute;
    width: 100%;
    bottom: 0;
    color : white;
    border-bottom-left-radius: 31px;
    border-bottom-right-radius: 30px;
    h4{
        text-align : center;
        font-size: 18px;
    }
`

const Overlay = styled(motion.div)`
    position: fixed;
    top:0;
    width:100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    opacity:0;
`;

const BigMovie = styled(motion.div)`
    position: absolute;
    width: 40vw;
    background-color: red;
    z-index: 999;
    height:80vh;
    left : 0;
    right : 0;
    margin : 0 auto;
`

const infoVariants = {
    hover : {
        opacity :1,
        transition : {
            duration:0.3,
            type:"tween"
        }
    },
}

const rowVariants = {
    hidden : {
        x: window.outerWidth + 10,
    },
    visible : {
        x: 0,
    },
    exit : {
        x: -window.outerWidth -10
    }

}

const boxVariants = {
    normal : {
        scale : 1
    },
    hover : {
        scale : 1.3,
        y: -50,
        transition : {
            duration : 0.3,
            type : "tween",
        }
    }
}

const Slide = ()=>{
    const [index, setIndex] = useState(0);
    const [leaving,setLeaving] = useState(false);
    const navigate = useNavigate();
    const movieMatch = useMatch("/movies/:movieId")
    const {scrollY} = useScroll();

    const offset = 6; //페이지 당 보여줄 <Box /> 개수

    const {data} = useQuery(["movieList"],getData) //useQuery를 이용하여 데이터 가져오기.

    const increaseIndex = () => {
        if(leaving) return;
        toggleLeaving()
        const totalMovies = data?.results.length -1;
        const maxIndex = Math.floor(totalMovies/offset) -1;
        setIndex((prev) => prev === maxIndex ? 0 : prev + 1);
    }

    const toggleLeaving = ()=> setLeaving((prev) => !prev)
    
    const onBoxClicked = (movieId) =>{
        navigate(`/movies/${movieId}`)
    }

    const onOverlayClick = ()=> navigate("/");
    
    

    return(
        <>
        <Button onClick={increaseIndex}>
            <ForwardOutlined />
        </Button>
        <Slider>
            <AnimatePresence initial={false} onExitComplete={toggleLeaving}>
                <Row
                    variants={rowVariants}
                    initial="hidden"
                    animate="visible"
                    exit = "exit"
                    transition = {{ type : "tween", duration:1.2 }}
                    key={index}
                >
                    {data?.results.slice(1).slice(offset*index,offset*index+offset).map((movie)=>(
                        <Box
                            key={movie.id}
                            whileHover = "hover"
                            initial = "normal"
                            layoutId={movie.id + ""}
                            variants={boxVariants}
                            onClick={()=>onBoxClicked(movie.id)}
                            transition = {{ type : "tween" }}
                            bgphoto={makeImagePath(movie.backdrop_path,"w500")}
                         >
                            <Info variants={infoVariants}>
                                <h4>{movie.title}</h4>
                            </Info>
                        </Box>
                    ))}
                </Row>
            </AnimatePresence>
        </Slider>
        <AnimatePresence>
        {
            movieMatch ? (
                <>
                    <Overlay
                        onClick={onOverlayClick}
                        exit={{opacity:0}}
                        animate = {{ opacity:1 }}
                    />
                    <BigMovie
                        style={{ top: scrollY.get() + 100 }}
                        layoutId={movieMatch.params.movieId}
                    >
                        hello
                    </BigMovie>
                </>
            ): null
        }
        </AnimatePresence>
        </>
    )
}

export default Slide;